// exercicio 3
const exercicio3Botao1 = () => {
    const img = document.querySelectorAll('#exercicio3_1')[0];
    if (img.offsetParent === null) {
        img.style.display = '';
    } else {
        img.style.display = 'none';
    }
}

const exercicio3Botao2 = () => {
    const imgs = document.querySelectorAll('#exercicio3_container img');
    Array.from(imgs).forEach((img) => {
        if (img.offsetParent === null) {
            img.style.display = '';
        } else {
            img.style.display = 'none';
        }
    })
}

