// exercicio 7
const exercicio7botao1 = () => {
    const input_num = document.getElementById('exercicio7input').value;
    if (input_num != "" && Number.isFinite(Number(input_num))) {
        const row = document.querySelector(`#exercicio7_container tbody tr:nth-child(${input_num})`);
        if (row) {
            row.remove();
        } else {
            alert('Linha não encontrada');
        }
    } else {
        alert('Digite um número válido.');
    }
}

